@import url(https://fonts.googleapis.com/css?family=Bebas+Neue|Montserrat&display=swap);
body {
    font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Bebas Neue', sans-serif;
}

.btn {
    font-family: 'Bebas Neue', sans-serif;
    letter-spacing: .2em;
    text-transform: uppercase;
}

.vh-100 {
    min-height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.avatar.avatar-xxl {
    width: 7rem;
    min-width: 7rem;
    height: 7rem;
}
@media (min-width: 768.98px) {
    .avatar.avatar-xxl {
      width: 10rem;
      min-width: 10rem;
      height: 10rem;
  }
}

.avatar {
    display: inline-block;
    position: relative;
    width: 3rem;
    height: 3rem;
    text-align: center;
    border: #dee2e6;
    border-radius: 50%;
    background: #fff;
    box-shadow: 0 0 1rem rgba(0,0,0,0.15);
    line-height: 3rem;
}

.icon-rounded.icon-rounded-sm {
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
}

.bg-primary-light {
    background-color: #eaedfe;
}
.icon-rounded {
    display: inline-block;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    text-align: center;
    line-height: 4rem;
}
html,
body {
  overflow-x: hidden; /* Prevent scroll on narrow devices */
}


@media (max-width: 991.98px) {
  .offcanvas-collapse {
    position: fixed;
    bottom: 0;
    left: 100%;
    width: 100%;
    padding-right: 1rem;
    padding-left: 1rem;
    overflow-y: auto;
    visibility: hidden;
    transition: transform .3s ease-in-out, visibility .3s ease-in-out;
  }
  .offcanvas-collapse.open {
    visibility: visible;
    transform: translateX(-100%);
  }
}

.nav-scroller {
  position: relative;
  z-index: 2;
  height: 2.75rem;
  overflow-y: hidden;
}

.nav-scroller .nav {
  display: flex;
  flex-wrap: nowrap;
  padding-bottom: 1rem;
  margin-top: -1px;
  overflow-x: auto;
  color: rgba(255, 255, 255, .75);
  text-align: center;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}

.nav-underline .nav-link {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: .875rem;
  color: #6c757d;
}

.nav-underline .nav-link:hover {
  color: #007bff;
}

.nav-underline .active {
  font-weight: 500;
  color: #343a40;
}

.text-white-50 { color: rgba(255, 255, 255, .5); }

.bg-purple { background-color: #6f42c1; }

.lh-100 { line-height: 1; }
.lh-125 { line-height: 1.25; }
.lh-150 { line-height: 1.5; }
.text-sm {
    font-size: .875rem;
}

.subtitle {
    margin-bottom: 0;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    font-weight: bold;
}
.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 32px;
    height: 32px;
}
.commits-listing-padded {
    padding-left: 39px;
}

.commits-listing {
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 15px;
}
.commit-group-title {
    margin-top: 15px;
    margin-left: -34px;
    color: #586069;
}
.commits-listing:before {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 14px;
    z-index: -1;
    display: block;
    width: 2px;
    content: "";
    background-color: #eff1f3;
}
.commit-group {
    margin-top: 10px;
    list-style-type: none;
}
.commit-group-title .git-commit {
    margin-right: 17px;
    color: #c6cbd1; 
    background: #fff;
}
.git-commit {
    margin-right: 17px;
    color: #c6cbd1;
    background: #fff;
}
.hover-animate {
    transition: all 0.2s ease-in-out;
}
.hover-animate:hover, .hover-animate:focus {
    transform: translate3d(0, -2px, 5px);
}
.text-linkedin {
    color: #0084bf!important
}
.text-github {
    color: #24292e!important
}
.text-resume {
    color: #DC3513!important
}
